import axios from 'axios';
import actions from '../state/actions';

export default async ( dispatch ) => {
        try {
            const res = await axios.get( '/auth/ping' );
            if (res.data.success) {
                return dispatch(actions.logIn(res.data.payload));
            } else {
                return dispatch(actions.logOut());
            }
        } catch (err) {
            return dispatch(actions.logOut());
        }
}