import axios from 'axios';

export default async (path) => {
    try {
        const res = await axios.get(`/api/resource/${path}`);
        if (res.data.success) {
            return {
                success: true,
                payload: res.data.payload
            }
        } else {
            console.log(res.data);
            return {
                success: false
            }
        }
    } catch (err) {
        console.log(err);
        return {
            success: false
        }
    }
}