//Import dependencies
import React, {Fragment, useEffect} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';

//Import components
import Navbar from '../Partials/Navbar';
import Footer from '../Partials/Footer';

//Import util
import util from '../../util';

//Import actions
import actions from '../../state/actions';

//Map State to Props Function
const mapStateToProps = (state) => {
    return {student: state.loginReducer}
}

//Build container
const MyWingmait = (props) => {
    useEffect(() => {
        const main = async () => {
            try {
                const res = await util.resource.get("mypage");
            } catch (err) {
                err.response
                ? util.notify(props.dispatch, err.response.data, "is-danger")
                : console.log(err);
            }
        }
        main();
    }, [props.dispatch])
    return (
        <Fragment>
            <Navbar />
            <section className="my-wingmait">
                <div className="hero container">
                    <img className="img banner" src={process.env.PUBLIC_URL + '/images/my-wingmait.jpg'} alt="online" />
                    <div className="overlay banner"></div>
                    <div className="content banner">
                        <div className="container">
                            <h1 className="is-size-3 has-text-centered">Upskill Now!</h1>
                        </div>
                    </div>
                </div>
                <div className="container">
                    <h1 className="is-size-4">Our Courses</h1>
                </div>
            </section>
            <Footer />
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(MyWingmait);