import { LOGIN_USER, LOGOUT_USER } from '../actions/types';

const initialState = {
    isLoggedin: false,
    name: '',
    payments: [],
    enrolments: []
};

export default (state=initialState, action={}) => {
    const type = action.type;
    const payload = action.payload;
    switch(type) {
        case LOGIN_USER :
            return {isLoggedin: true, name: payload.name, payments: payload.payments, enrolments: payload.enrolments};
        case LOGOUT_USER :
            return initialState;
        default :
            return state;
    }
}