import React, {Fragment} from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import './index.scss';

import Notifications from '../Partials/Notifications';

import ProtectedRoute from '../ProtectedRoute';
import MyWingmait from '../MyWingmait';
import MyAccount from '../MyAccount';
import MyCourses from '../MyCourses';
import MyPayments from '../MyPayments';

import Login from '../Login';
import Signup from '../Signup';
import Home from '../Home';
import Privacy from '../Privacy';
import Terms from '../Terms';
import Contact from '../Contact';
import Sitemap from '../Sitemap';

export default(props) => (
    <Fragment>
        <Notifications />
        <Router>
            <Switch>
                <Route exact path='/' {...props} component={Home}/>
                <Route exact path='/login' {...props} component={Login}/>
                <Route exact path='/signup' {...props} component={Signup}/>
                <Route exact path='/contact' {...props} component={Contact}/>
                <Route exact path='/privacy' {...props} component={Privacy}/>
                <Route exact path='/terms' {...props} component={Terms}/>
                <Route exact path='/sitemap' {...props} component={Sitemap}/>
                <ProtectedRoute exact path='/my-wingmait' {...props} component={MyWingmait}/>
                <ProtectedRoute exact path='/my-account' {...props} component={MyAccount}/>
                <ProtectedRoute exact path='/my-courses' {...props} component={MyCourses}/>
                <ProtectedRoute exact path='/my-payments' {...props} component={MyPayments}/>
            </Switch>
        </Router>
    </Fragment>
)