import React, {Fragment} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';
import SocialButton from '../Partials/SocialButton';

//Import components
import Navbar from '../Partials/Navbar';
import Footer from '../Partials/Footer';

//Import utils
import util from '../../util';

const mapStateToProps = (state) => {
    return {}
}

const Signup = (props) => {

    const onSubmitSignup = async(e) => {
        e.preventDefault();
        try {
            let form = document.getElementById('signUp');
            let payload = new FormData(form);
            console.log(payload);
            await axios.post('/api/student', payload);
            util.notify(props.dispatch, "Registered. Log in now.", "is-success");
            form.reset();
            window.open('/login', "_self");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    return (
        <Fragment>
            <Navbar/>
            <section className="signup">
                <div className="box">
                    <p className="is-size-6 has-text-centered has-text-weight-semibold">Sign Up for Wingmait</p>
                    <hr/>
                    <div className="button-holder">
                        <SocialButton provider="google"/>
                    </div>
                    <div className="button-holder">
                        <SocialButton provider="facebook"/>
                    </div>
                    <div className="is-flex divider">
                        <div className="line"></div>
                        <p>or</p>
                        <div className="line"></div>
                    </div>
                    <form
                        id="signUp"
                        className="form"
                        encType="multipart/form-data"
                        onSubmit={onSubmitSignup}>
                        <div className="control has-icons-left">
                            <input
                                className="input"
                                type="text"
                                placeholder="Full Name"
                                required
                                name="name"/>
                            <span className="icon is-normal is-left">
                                <img src={process.env.PUBLIC_URL + '/images/name-icon-input.png'} alt="name"/>
                            </span>
                        </div>
                        <div className="control has-icons-left">
                            <input
                                className="input"
                                type="email"
                                placeholder="Email Id"
                                required
                                name="email"/>
                            <span className="icon is-normal is-left">
                                <img src={process.env.PUBLIC_URL + '/images/email-icon-input.png'} alt="email"/>
                            </span>
                        </div>
                        <p className="is-size-7">Password must be 6 characters or longer</p>
                        <div className="control has-icons-left">
                            <input
                                className="input"
                                type="password"
                                placeholder="Password"
                                minLength="6"
                                name="password"/>
                            <span className="icon is-normal is-left">
                                <img
                                    src={process.env.PUBLIC_URL + '/images/password-icon-input.png'}
                                    alt="password"/>
                            </span>
                        </div>
                        <div className="control has-icons-left">
                            <input
                                className="input"
                                type="password"
                                placeholder="Confirm Password"
                                required
                                minLength="6"
                                name="confirm_password"/>
                            <span className="icon is-normal is-left">
                                <img
                                    src={process.env.PUBLIC_URL + '/images/password-icon-input.png'}
                                    alt="password"/>
                            </span>
                        </div>
                        <button type="submit" className="button">Sign Up</button>
                    </form>
                    <p className="has-text-centered is-size-7 foot-note">Already have an account?&nbsp;
                        <a href="/login">Log In</a>
                    </p>
                </div>
            </section>
            <Footer/>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Signup);