import React, {Fragment} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';
import SocialButton from '../Partials/SocialButton';

//Import components
import Navbar from '../Partials/Navbar'
import Footer from '../Partials/Footer'

//Import actions
import actions from '../../state/actions';
//Import utils
import util from '../../util';

const mapStateToProps = (state) => {
    return {}
}

const Login = (props) => {
    const onSubmitLogin = async(e) => {
        e.preventDefault();
        try {
            let form = document.getElementById("logInForm");
            let payload = new FormData(form);
            let res = await axios.post('/auth/local', payload);
            if (res.data.success) {
                props.dispatch(actions.logIn(res.data.payload));
                window.open("/my-wingmait", "_self");
            }
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data, "is-danger")
                : console.log(err);
        }
    }
    return (
        <Fragment>
            <Navbar/>
            <section className="login">
                <div className="box">
                    <p className="is-size-6 has-text-centered has-text-weight-semibold">Log In to Wingmait</p>
                    <hr/>
                    <div className="button-holder">
                        <SocialButton provider="google"/>
                    </div>
                    <div className="button-holder">
                        <SocialButton provider="facebook"/>
                    </div>
                    <div className="is-flex divider">
                        <div className="line"></div>
                        <p>or</p>
                        <div className="line"></div>
                    </div>
                    <form id="logInForm" className="form" onSubmit={onSubmitLogin}>
                        <label>Email Id</label>
                        <div className="control has-icons-left">
                            <input
                                className="input"
                                type="email"
                                placeholder="Email"
                                required
                                name="email"/>
                            <span className="icon is-normal is-left">
                                <img src={process.env.PUBLIC_URL + '/images/email-icon-input.png'} alt="email"/>
                            </span>
                        </div>
                        <div className="is-flex password-label">
                            <label>Password</label>
                            <a className="is-size-7" href="/forgot-password">Forgot password?</a>
                        </div>
                        <div className="control has-icons-left">
                            <input
                                className="input"
                                type="password"
                                placeholder="Password"
                                required
                                name="password"/>
                            <span className="icon is-normal is-left">
                                <img
                                    src={process.env.PUBLIC_URL + '/images/password-icon-input.png'}
                                    alt="password"/>
                            </span>
                        </div>
                        <button type="submit" className="button">Log In</button>
                    </form>
                    <p className="has-text-centered is-size-7 foot-note">Don't have an account?&nbsp;
                        <a href="/signup">Sign Up</a>
                    </p>
                </div>
            </section>
            <Footer/>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Login);